<template>
  <div class="home-view">
    <!-- 使用背景组件 -->
    <BackGround />
    <TopMenu />
  </div>
  <div class="home">
    <!-- 管理员切换按钮 -->
    <div class="admin-switch" v-if="isAdmin">
      <div class="switch-tabs">
        <button 
          :class="['tab-btn', { active: currentTab === 'user' }]"
          @click="currentTab = 'user'"
        >
          <i class="fas fa-user"></i> 分享管理
        </button>
        <button 
          :class="['tab-btn', { active: currentTab === 'admin' }]"
          @click="currentTab = 'admin'"
        >
          <i class="fas fa-cog"></i> 管理员模式
        </button>
      </div>
    </div>

    <!-- 用户分享管理 -->
    <template v-if="currentTab === 'user'">
      <ShareFile />
      <ShareList />
    </template>
    
    <!-- 管理员分享管理 -->
    <template v-if="currentTab === 'admin'">
      <AdminFileView />
      <AdminView />
    </template>
  </div>
  <FootMenu />
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import TopMenu from '../components/Top-Menu.vue';
import BackGround from '../components/Back-Ground.vue';
import FootMenu from '../components/Foot-Menu.vue';
import ShareList from './child/share/Share-List.vue';
import ShareFile from './child/share/Share-File.vue';
import AdminFileView from './child/share/AdminFileView.vue';
import AdminView from './child/share/AdminView.vue';
import { getCurrentUser, ensureVisitJwt } from '@/utils/auth.js';

// 响应式数据
const currentTab = ref('user');
const currentUser = ref(null);

// 计算属性
const isAdmin = computed(() => {
  // 确保用户信息已加载且用户是管理员
  return currentUser.value && currentUser.value.is_admin === true;
});

// 生命周期
onMounted(async () => {
  try {
    // 确保JWT有效并获取用户信息
    const hasJwt = await ensureVisitJwt();
    
    if (hasJwt) {
      const user = getCurrentUser();
      currentUser.value = user;
    }
  } catch (error) {
    console.error('用户信息获取失败:', error);
  }
});
</script>

