<template>
  <div v-if="visible" class="modal-overlay" @click="handleOverlayClick">
    <div class="modal-content" @click.stop>
      <div class="modal-header">
        <h3 class="modal-title">分享信息</h3>
        <button class="modal-close" @click="handleClose">
          <i class="fas fa-times"></i>
        </button>
      </div>
      
      <div class="modal-body">
        <!-- 分享基本信息 -->
        <div class="share-info">
          <div class="info-row">
            <label>分享名称：</label>
            <span>{{ displayShareData?.share_name || '' }}</span>
          </div>
          <div class="info-row">
            <label>分享代码：</label>
            <span class="share-code">{{ displayShareData?.share_code || '' }}</span>
          </div>
          <div class="info-row">
            <label>文件ID：</label>
            <span>{{ displayShareData?.file_id || '' }}</span>
          </div>
        </div>
        
        <!-- 访问链接 -->
        <div class="form-group">
          <label class="form-label">访问链接</label>
          <div class="link-display">
            <input 
              type="text" 
              class="form-input readonly-text" 
              :value="shareLink"
              readonly
            />
            <button class="copy-btn" @click="copyLink" :disabled="!shareLink">
              <i class="fas fa-copy"></i>
            </button>
          </div>
        </div>
        
        <!-- 访问密码 -->
        <div class="form-group">
          <label class="form-label">访问密码</label>
          <div class="password-display">
            <div v-if="loading" class="password-text">
              正在加载密码...
            </div>
            <div v-else-if="displayShareData?.access_password" class="password-text">
              {{ displayShareData.access_password }}
            </div>
            <div v-else class="no-password-text">
              该分享无密码保护
            </div>
            <button 
              v-if="displayShareData?.access_password && !loading" 
              class="copy-btn" 
              @click="copyPassword"
            >
              <i class="fas fa-copy"></i>
            </button>
          </div>
        </div>
        
        <!-- 扩展数据 -->
        <div class="form-group" v-if="displayShareData?.extension && displayShareData.extension !== '{}'">
          <label class="form-label">扩展数据</label>
          <div class="extension-display">
            <textarea 
              class="form-input readonly-text" 
              :value="displayShareData.extension"
              readonly
              rows="3"
            ></textarea>
            <button class="copy-btn" @click="copyExtension">
              <i class="fas fa-copy"></i>
            </button>
          </div>
        </div>
        
        <!-- 复制成功提示 -->
        <div v-if="copySuccess" class="copy-success">
          <i class="fas fa-check"></i>
          {{ copyMessage }}
        </div>
      </div>
      
      <div class="modal-footer">
        <button class="modal-btn secondary" @click="copyAll">
          <i class="fas fa-copy"></i> 复制全部信息
        </button>
        <button class="modal-btn primary" @click="handleClose">
          关闭
        </button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, watch } from 'vue'

// Props
/* eslint-disable no-undef */
const props = defineProps({
  visible: {
    type: Boolean,
    default: false
  },
  shareData: {
    type: Object,
    default: null
  }
})

// Emits
const emit = defineEmits(['close'])
/* eslint-enable no-undef */

// 响应式数据
const copySuccess = ref(false)
const copyMessage = ref('')
const loading = ref(false)

// 完整的分享数据（可能需要重新获取）
const fullShareData = ref(null)

// 计算属性
const shareLink = computed(() => {
  const shareData = fullShareData.value || props.shareData
  if (!shareData?.share_code) return ''
  
  // 获取当前网站域名
  const domain = window.location.origin
  return `${domain}/download?code=${shareData.share_code}`
})

const displayShareData = computed(() => {
  return fullShareData.value || props.shareData
})

// 监听弹窗关闭，重置复制状态
watch(() => props.visible, (visible) => {
  if (!visible) {
    copySuccess.value = false
    copyMessage.value = ''
    fullShareData.value = null
  } else if (visible && props.shareData) {
    // 调试：输出接收到的分享数据
    console.log('ShareInfoModal 接收到的数据:', props.shareData)
    // 直接使用传入的数据，不再请求额外信息
    fullShareData.value = props.shareData
  }
})

// 方法
const handleOverlayClick = (e) => {
  if (e.target === e.currentTarget) {
    handleClose()
  }
}

const handleClose = () => {
  emit('close')
}

const copyToClipboard = async (text) => {
  try {
    await navigator.clipboard.writeText(text)
    return true
  } catch (error) {
    // 降级方案
    const textArea = document.createElement('textarea')
    textArea.value = text
    document.body.appendChild(textArea)
    textArea.select()
    try {
      document.execCommand('copy')
      document.body.removeChild(textArea)
      return true
    } catch (e) {
      document.body.removeChild(textArea)
      return false
    }
  }
}

const showCopySuccess = (message) => {
  copyMessage.value = message
  copySuccess.value = true
  setTimeout(() => {
    copySuccess.value = false
  }, 2000)
}

const copyLink = async () => {
  if (!shareLink.value) return
  
  const success = await copyToClipboard(shareLink.value)
  if (success) {
    showCopySuccess('访问链接已复制到剪贴板')
  } else {
    showCopySuccess('复制失败，请手动复制')
  }
}

const copyPassword = async () => {
  const shareData = displayShareData.value
  if (!shareData?.access_password) {
    showCopySuccess('该分享无密码保护')
    return
  }
  
  const password = shareData.access_password;
  if (!password) {
    showCopySuccess('密码信息未加载，请重新打开分享信息')
    return
  }
  
  const success = await copyToClipboard(password)
  if (success) {
    showCopySuccess('访问密码已复制到剪贴板')
  } else {
    showCopySuccess('复制失败，请手动复制')
  }
}

const copyExtension = async () => {
  const shareData = displayShareData.value
  if (!shareData?.extension) {
    showCopySuccess('该分享无扩展数据')
    return
  }
  
  const success = await copyToClipboard(shareData.extension)
  if (success) {
    showCopySuccess('扩展数据已复制到剪贴板')
  } else {
    showCopySuccess('复制失败，请手动复制')
  }
}

const copyAll = async () => {
  const shareData = displayShareData.value
  if (!shareData) return
  
  const passwordText = shareData.access_password || '无密码'
  const extensionText = shareData.extension && shareData.extension !== '{}' 
    ? shareData.extension 
    : '无扩展数据'
  
  const info = [
    `分享名称：${shareData.share_name || ''}`,
    `访问链接：${shareLink.value}`,
    `访问密码：${passwordText}`,
    `文件ID：${shareData.file_id || ''}`,
    `分享代码：${shareData.share_code || ''}`,
    `扩展数据：${extensionText}`
  ].join('\n')
  
  const success = await copyToClipboard(info)
  if (success) {
    showCopySuccess('分享信息已复制到剪贴板')
  } else {
    showCopySuccess('复制失败，请手动复制')
  }
}
</script>

<style>
@import './modal-styles.css';

</style>
