<template>
  <div class="file-list-container">
    <div class="file-card">
      <!-- 搜索框和批量操作栏 -->
      <div class="search-and-batch-actions">
        <div class="left-section">
          <h3>分享列表</h3>
          <div class="action-buttons">
            <button class="batch-btn" @click="handleBatchDelete" :disabled="!selectedFiles.length">
              <i class="fas fa-unlink"></i> 取消分享
            </button>
            <button class="batch-btn" @click="handleRefresh">
              <i class="fas fa-sync-alt"></i> 刷新
            </button>
          </div>
        </div>
        <div class="right-section">
          <div class="search-controls">
            <input
              type="text"
              v-model="searchQuery"
              placeholder="搜索分享名称..."
              class="search-input"
              @keyup.enter="handleSearch"
            />
            <button class="batch-btn" @click="handleSearch">
              <i class="fas fa-search"></i> 搜索
            </button>
            <button v-if="searchQuery" class="batch-btn" @click="handleClearSearch">
              <i class="fas fa-times"></i> 清除搜索
            </button>
          </div>
        </div>
      </div>

      <!-- 加载状态 -->
      <div v-if="loading" class="empty-state">
        <div class="loading-spinner"></div>
        加载中...
      </div>

      <!-- 空状态 -->
      <div v-else-if="!shares.length" class="empty-state">
        <div class="icon">
          <i class="fas fa-share-alt"></i>
        </div>
        <div class="title">暂无分享</div>
        <div class="description">
          请先创建分享链接
        </div>
      </div>

      <!-- 分享列表 -->
      <div v-else class="table-wrapper">
        <div class="table-min">
          <table class="file-table">
            <thead>
              <tr>
                <th><input type="checkbox" @change="toggleAll" :checked="isAllChecked" /></th>
                <th>分享名称</th>
                <th>分享代码</th>
                <th>文件ID</th>
                <th>创建时间</th>
                <th>访问次数</th>
                <th>下载次数</th>
                <th class="password-cell">密码保护</th>
                <th>操作</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="share in shares" :key="share.id">
                <td><input type="checkbox" v-model="selectedFiles" :value="share.id" /></td>
                <td>{{ share.share_name }}</td>
                <td class="share-code-cell">
                  <span class="share-code" @click="showShareInfo(share)" title="点击查看分享信息">
                    {{ share.share_code }}
                  </span>
                </td>
                <td>
                  <span class="file-id">{{ share.file_id || '-' }}</span>
                </td>
                <td>{{ formatDate(share.created_time) }}</td>
                <td class="stats-cell">
                  <span class="stats-number">{{ share.view_count }}</span>
                </td>
                <td class="stats-cell">
                  <span class="stats-number">{{ share.download_count }}</span>
                </td>
                <td>
                  <button class="password-btn" @click="showPassword(share)">
                    {{ share.access_password ? '有密码' : '无密码' }}
                  </button>
                </td>
                <td>
                  <button class="action-btn" @click="handleModifyShare(share)">编辑</button>
                  <button class="action-btn" @click="handleDeleteShare(share)" style="margin-left: 8px;">删除</button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <!-- 分页 -->
      <div class="pagination-container" v-if="pagination.total_pages > 1">
        <span class="page-info">
          第 {{ pagination.page }} 页，共 {{ pagination.total_pages }} 页 ({{ pagination.total }} 项)
        </span>
        
        <div class="pagination">
          <button :disabled="!pagination.has_prev" @click="changePage(pagination.page - 1)">
            <i class="fas fa-chevron-left"></i> 上一页
          </button>
          <button 
            v-for="page in getPageNumbers()" 
            :key="page" 
            :class="{ 'active': page === pagination.page }" 
            @click="changePage(page)"
            v-show="page !== '...'"
          >
            {{ page }}
          </button>
          <span v-if="getPageNumbers().includes('...')" class="page-ellipsis">...</span>
          <button :disabled="!pagination.has_next" @click="changePage(pagination.page + 1)">
            下一页 <i class="fas fa-chevron-right"></i>
          </button>
        </div>
      </div>
    </div>

    <!-- 编辑分享弹窗 -->
    <ShareEditModal
      v-if="editModal.visible"
      :visible="editModal.visible"
      :mode="editModal.mode"
      :share-data="editModal.shareData"
      :loading="editModal.loading"
      @close="closeEditModal"
      @confirm="handleEditConfirm"
    />

    <!-- 分享信息查看弹窗 -->
    <ShareInfoModal
      v-if="infoModal.visible"
      :visible="infoModal.visible"
      :share-data="infoModal.shareData"
      @close="closeInfoModal"
    />
  </div>
</template>

<script setup>
import { ref, computed, onMounted, inject } from 'vue'
import { ensureVisitJwt, getVisitJwt } from '@/utils/auth.js'
import ShareEditModal from './ShareEditModal.vue'
import ShareInfoModal from './ShareInfoModal.vue'
import '@/panel/child/file/file-list.css'
import '@/panel/child/share/share-styles.css'
import '@/panel/child/share/modal-styles.css'

// 注入全局组件
const notification = inject('notification')
const confirm = inject('confirm')

// 响应式数据
const shares = ref([])
const selectedFiles = ref([])
const searchQuery = ref('')
const loading = ref(false)
const currentPage = ref(1)
const pagination = ref({
  page: 1,
  per_page: 20,
  total: 0,
  total_pages: 1,
  has_next: false,
  has_prev: false
})

// 弹窗状态
const editModal = ref({
  visible: false,
  mode: 'edit', // create | edit
  shareData: null,
  loading: false
})

const infoModal = ref({
  visible: false,
  shareData: null
})

// 计算属性
const isAllChecked = computed(() => {
  return shares.value.length > 0 && selectedFiles.value.length === shares.value.length
})

// API调用辅助函数
const apiCall = async (action, data = {}) => {
  try {
    // 确保有访问JWT
    const hasJwt = await ensureVisitJwt()
    if (!hasJwt) {
      notification.open({
        type: 'error',
        title: '认证失败',
        message: '请重新登录',
        onClose: () => window.location.reload()
      })
      return null
    }

    const formData = new FormData()
    formData.append('action', action)
    
    // 添加JWT访问令牌
    const visitJwt = getVisitJwt()
    if (visitJwt) {
      formData.append('jwt', visitJwt)
    }
    
    // 添加其他参数
    for (const [key, value] of Object.entries(data)) {
      if (value !== null && value !== undefined && value !== '') {
        formData.append(key, value)
      }
    }

    const response = await fetch('/api/share/share-api.php', {
      method: 'POST',
      body: formData,
      credentials: 'include'
    })

    const result = await response.json()
    
    if (!result.success) {
      throw new Error(result.message || '操作失败')
    }
    
    return result
  } catch (error) {
    console.error('API调用失败:', error)
    throw error
  }
}

// 方法
const toggleAll = () => {
  if (isAllChecked.value) {
    selectedFiles.value = []
  } else {
    selectedFiles.value = shares.value.map(share => share.id)
  }
}

const formatDate = (dateStr) => {
  return new Date(dateStr).toLocaleString('zh-CN')
}

const getPageNumbers = () => {
  const total = pagination.value.total_pages
  const current = pagination.value.page
  const pages = []
  
  if (total <= 7) {
    for (let i = 1; i <= total; i++) {
      pages.push(i)
    }
  } else {
    pages.push(1)
    if (current > 4) pages.push('...')
    
    const start = Math.max(2, current - 1)
    const end = Math.min(total - 1, current + 1)
    
    for (let i = start; i <= end; i++) {
      pages.push(i)
    }
    
    if (current < total - 3) pages.push('...')
    pages.push(total)
  }
  
  return pages
}

const changePage = (page) => {
  if (page >= 1 && page <= pagination.value.total_pages) {
    currentPage.value = page
    loadShares()
  }
}

const handleSearch = () => {
  currentPage.value = 1
  loadShares()
}

const handleRefresh = () => {
  searchQuery.value = ''
  currentPage.value = 1
  loadShares()
}

const handleClearSearch = () => {
  searchQuery.value = ''
  currentPage.value = 1
  loadShares()
}

// 弹窗操作方法
const showShareInfo = (share) => {
  infoModal.value = {
    visible: true,
    shareData: share
  }
}

const closeInfoModal = () => {
  infoModal.value.visible = false
}

const showPassword = (share) => {
  // 直接显示分享信息弹窗
  showShareInfo(share)
}

const handleModifyShare = (share) => {
  editModal.value = {
    visible: true,
    mode: 'edit',
    shareData: share,
    loading: false
  }
}

const closeEditModal = () => {
  editModal.value.visible = false
}

const handleEditConfirm = async (formData) => {
  editModal.value.loading = true
  
  try {
    const requestData = {
      share_id: formData.shareId,
      share_name: formData.shareName,
      access_password: formData.accessPassword,
      extension: formData.extension
    }
    
    // 如果提供了文件ID，添加到请求中
    if (formData.fileId) {
      requestData.file_id = formData.fileId
    }
    
    const result = await apiCall('update_share', requestData)
    
    if (result) {
      notification.open({
        type: 'success',
        title: '修改成功',
        message: '分享信息已更新',
        duration: 3000
      })
      
      closeEditModal()
      await loadShares()
    }
  } catch (error) {
    notification.open({
      type: 'error',
      title: '操作失败',
      message: error.message
    })
  } finally {
    editModal.value.loading = false
  }
}

const handleDeleteShare = async (share) => {
  confirm.open({
    title: '确认删除',
    message: `确定要删除分享"${share.share_name}"吗？`,
    onConfirm: async () => {
      try {
        const result = await apiCall('delete_share', {
          share_id: share.id
        })
        
        if (result) {
          notification.open({
            type: 'success',
            title: '删除成功',
            message: '分享已删除',
            duration: 3000
          })
          await loadShares()
        }
      } catch (error) {
        notification.open({
          type: 'error',
          title: '删除失败',
          message: error.message
        })
      }
    }
  })
}

const handleBatchDelete = async () => {
  if (!selectedFiles.value.length) {
    notification.open({
      type: 'warning',
      title: '提示',
      message: '请先选择要删除的分享',
      duration: 3000
    })
    return
  }
  
  confirm.open({
    title: '确认批量删除',
    message: `确定要删除选中的 ${selectedFiles.value.length} 个分享吗？`,
    onConfirm: async () => {
      try {
        const promises = selectedFiles.value.map(shareId => {
          return apiCall('delete_share', {
            share_id: shareId
          })
        })
        
        await Promise.all(promises)
        selectedFiles.value = []
        
        notification.open({
          type: 'success',
          title: '批量删除成功',
          message: '已删除所有选中的分享',
          duration: 3000
        })
        
        await loadShares()
      } catch (error) {
        notification.open({
          type: 'error',
          title: '批量删除失败',
          message: error.message
        })
      }
    }
  })
}

const loadShares = async () => {
  try {
    loading.value = true
    
    // 确保有访问JWT
    const hasJwt = await ensureVisitJwt()
    if (!hasJwt) {
      notification.open({
        type: 'error',
        title: '认证失败',
        message: '请重新登录',
        onClose: () => window.location.reload()
      })
      return
    }
    
    // 构建查询参数
    const params = new URLSearchParams({
      page: currentPage.value,
      per_page: 20
    })
    
    if (searchQuery.value.trim()) {
      params.append('action', 'search_shares')
      params.append('keyword', searchQuery.value.trim())
    } else {
      params.append('action', 'list_shares')
    }
    
    // 添加JWT参数
    const visitJwt = getVisitJwt()
    if (visitJwt) {
      params.append('jwt', visitJwt)
    }
    
    const response = await fetch(`/api/share/share-api.php?${params.toString()}`, {
      method: 'GET',
      credentials: 'include'
    })
    
    const result = await response.json()
    if (result.success) {
      shares.value = result.data || []
      pagination.value = result.pagination || {}
    } else {
      throw new Error(result.message)
    }
  } catch (error) {
    console.error('加载分享列表失败:', error)
    notification.open({
      type: 'error',
      title: '加载失败',
      message: error.message
    })
  } finally {
    loading.value = false
  }
}

// 生命周期
onMounted(() => {
  loadShares()
})

// 暴露方法给父组件
// eslint-disable-next-line no-undef
defineExpose({
  refresh: loadShares
})
</script>

<style scoped>
.table-min {
  min-width: 1200px;
}

.create-share-btn {
  background: linear-gradient(135deg, #17a2b8, #138496) !important;
  color: white !important;
  font-weight: 500;
  border: none !important;
  box-shadow: 0 2px 4px rgba(23, 162, 184, 0.3);
}

.create-share-btn:hover {
  background: linear-gradient(135deg, #138496, #117a8b) !important;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(23, 162, 184, 0.4);
}

.file-id, .user-id {
  font-family: monospace;
  font-size: 13px;
  color: #b0b0b0;
  background: none;
  padding: 2px 6px;
  border-radius: 3px;
  border: none;
  font-weight: bold;
  letter-spacing: 0.5px;
}

.file-id.selected, .user-id.selected {
  color: #2196f3;
  font-weight: bold;
}
</style>

