<template>
  <div v-if="visible" class="modal-overlay" @click="handleOverlayClick">
    <div class="modal-content" @click.stop>
      <div class="modal-header">
        <h3 class="modal-title">{{ isEditMode ? '编辑分享' : '创建分享' }}</h3>
        <button class="modal-close" @click="handleCancel">
          <i class="fas fa-times"></i>
        </button>
      </div>
      
      <div class="modal-body">
        <div class="form-group">
          <label class="form-label">分享名称</label>
          <input
            type="text"
            v-model="formData.shareName"
            placeholder="请输入分享名称（最多8个字符）"
            class="form-input"
            maxlength="8"
            :disabled="loading"
          />
        </div>
        
        <div class="form-group">
          <label class="form-label">访问密码（可选）</label>
          <input
            type="text"
            v-model="formData.accessPassword"
            placeholder="留空表示无密码访问"
            class="form-input"
            maxlength="20"
            :disabled="loading"
          />
          <div v-if="isEditMode && originalPassword" class="form-hint">
            原密码：{{ originalPassword }}
          </div>
        </div>
        
        <div class="form-group">
          <label class="form-label">
            <span>扩展功能（可选）</span>
            <label class="switch">
              <input type="checkbox" v-model="extensionEnabled" :disabled="loading">
              <span class="slider"></span>
            </label>
          </label>
          
          <!-- 扩展功能详细配置 -->
          <div v-if="extensionEnabled" class="extension-config">
            <div class="form-subgroup">
              <label class="form-sublabel">按钮名称</label>
              <input
                type="text"
                v-model="extensionData.btn"
                placeholder="例如：打赏支持"
                class="form-input"
                maxlength="20"
                :disabled="loading"
              />
            </div>
            
            <div class="form-subgroup">
              <label class="form-sublabel">按钮链接</label>
              <input
                type="url"
                v-model="extensionData.url"
                placeholder="例如：https://example.com"
                class="form-input"
                :disabled="loading"
              />
            </div>
            
            <div class="form-subgroup">
              <label class="form-sublabel">图片链接</label>
              <input
                type="url"
                v-model="extensionData.image"
                placeholder="例如：https://example.com/qrcode.png"
                class="form-input"
                :disabled="loading"
              />
            </div>
            
            <div class="form-hint">
              可以只设置按钮、只设置图片，或者两者都设置
            </div>
          </div>
        </div>
        
        <!-- 文件选择区域 - 仅在创建模式显示 -->
        <div v-if="!isEditMode" class="form-group">
          <label class="form-label">选择文件</label>
          <div class="file-selector readonly">
            <div v-if="!selectedFile" class="no-file-selected">
              <i class="fas fa-file"></i>
              <span>请选择要分享的文件</span>
            </div>
            <div v-else class="selected-file">
              <i :class="getFileTypeIcon(selectedFile.file_type || selectedFile.type)" class="file-icon"></i>
              <div class="file-info">
                <div class="file-name">{{ selectedFile.file_name || selectedFile.name }}</div>
                <div class="file-path">{{ selectedFile.file_path || selectedFile.path }}</div>
              </div>
            </div>
          </div>
        </div>
        
        <!-- 编辑模式显示当前文件信息 -->
        <div v-else-if="shareData" class="form-group">
          <label class="form-label">分享文件</label>
          <div class="selected-file readonly">
            <i :class="getFileTypeIcon(shareData.file_type)" class="file-icon"></i>
            <div class="file-info">
              <div class="file-name">文件ID: {{ shareData.file_id }}</div>
              <div class="file-path">{{ shareData.file_path || '文件路径信息' }}</div>
            </div>
          </div>
        </div>

        <!-- 编辑模式：文件ID输入 -->
        <div v-if="isEditMode" class="form-group">
          <label class="form-label">文件ID</label>
          <input
            type="text"
            v-model="formData.fileId"
            class="form-input"
            placeholder="输入新的文件ID（留空表示不修改）"
            :disabled="loading"
          />
          <div class="form-hint">留空表示不修改当前关联的文件</div>
        </div>
      </div>
      
      <div class="modal-footer">
        <button class="modal-btn secondary" @click="handleCancel" :disabled="loading">
          取消
        </button>
        <button class="modal-btn primary" @click="handleConfirm" :disabled="loading || !canConfirm">
          <span v-if="loading" class="loading-spinner"></span>
          {{ loading ? (isEditMode ? '保存中...' : '创建中...') : (isEditMode ? '保存' : '创建分享') }}
        </button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, watch } from 'vue'

// Props
/* eslint-disable no-undef */
const props = defineProps({
  visible: {
    type: Boolean,
    default: false
  },
  mode: {
    type: String,
    default: 'create', // 'create' | 'edit'
    validator: (value) => ['create', 'edit'].includes(value)
  },
  shareData: {
    type: Object,
    default: null
  },
  selectedFile: {
    type: Object,
    default: null
  },
  loading: {
    type: Boolean,
    default: false
  }
})

// Emits
const emit = defineEmits(['close', 'confirm', 'select-file'])
/* eslint-enable no-undef */

// 表单数据
const formData = ref({
  shareName: '',
  accessPassword: '',
  extension: '',
  fileId: ''
})

// 扩展数据相关
const extensionEnabled = ref(false)
const extensionData = ref({
  btn: '',
  url: '',
  image: ''
})

// 计算属性
const isEditMode = computed(() => props.mode === 'edit')

const originalPassword = computed(() => {
  return props.shareData?.access_password || ''
})

const canConfirm = computed(() => {
  if (isEditMode.value) {
    return formData.value.shareName.trim()
  } else {
    return formData.value.shareName.trim() && props.selectedFile
  }
})

// 监听数据变化，初始化表单
watch(() => props.shareData, (newData) => {
  if (newData && isEditMode.value) {
    formData.value.shareName = newData.share_name || ''
    formData.value.accessPassword = newData.access_password || ''
    formData.value.fileId = newData.file_id ? String(newData.file_id) : ''
    // 处理扩展数据
    let extensionObj = {}
    try {
      extensionObj = JSON.parse(newData.extension || '{}')
    } catch (e) {
      extensionObj = {}
    }
    extensionData.value = {
      btn: extensionObj.btn || '',
      url: extensionObj.url || '',
      image: extensionObj.image || ''
    }
    // 只要有扩展数据字段，开关就为true，否则为false
    extensionEnabled.value = !!(extensionData.value.btn || extensionData.value.url || extensionData.value.image)
  }
}, { immediate: true })

// 监听弹窗显示状态
watch(() => props.visible, (visible) => {
  if (visible && !isEditMode.value) {
    // 创建模式重置表单
    formData.value.shareName = ''
    formData.value.accessPassword = ''
    formData.value.fileId = ''
    extensionEnabled.value = false
    extensionData.value = {
      btn: '',
      url: '',
      image: ''
    }
  }
})

// 获取文件类型图标
const getFileTypeIcon = (type) => {
  const iconMap = {
    'image': 'fas fa-image file-type-icon image',
    'video': 'fas fa-video file-type-icon video',
    'audio': 'fas fa-music file-type-icon audio',
    'document': 'fas fa-file-alt file-type-icon document',
    'archive': 'fas fa-file-archive file-type-icon archive',
    'pdf': 'fas fa-file-pdf file-type-icon document',
    'text': 'fas fa-file-alt file-type-icon document',
    'code': 'fas fa-code file-type-icon document',
    'unknown': 'fas fa-file file-type-icon'
  }
  return iconMap[type] || iconMap['unknown']
}

// 事件处理
const handleOverlayClick = (e) => {
  if (e.target === e.currentTarget) {
    handleCancel()
  }
}

const handleCancel = () => {
  emit('close')
}

const handleConfirm = () => {
  if (!canConfirm.value) return
  
  // 构建扩展数据
  let extensionJson = '{}'
  if (extensionEnabled.value) {
    const ext = {}
    if (extensionData.value.btn && extensionData.value.btn.trim()) {
      ext.btn = extensionData.value.btn.trim()
    }
    if (extensionData.value.url && extensionData.value.url.trim()) {
      ext.url = extensionData.value.url.trim()
    }
    if (extensionData.value.image && extensionData.value.image.trim()) {
      ext.image = extensionData.value.image.trim()
    }
    extensionJson = JSON.stringify(ext)
  }
  
  const data = {
    shareName: formData.value.shareName.trim(),
    extension: extensionJson
  }
  
  // 处理密码字段 - 确保空字符串也能被传递
  data.accessPassword = formData.value.accessPassword
  
  if (isEditMode.value) {
    // 尝试多个可能的ID字段名
    data.shareId = props.shareData.share_id || props.shareData.id
    // 如果提供了文件ID，添加到数据中
    if (formData.value.fileId.trim()) {
      data.fileId = parseInt(formData.value.fileId.trim())
    }
  }
  
  emit('confirm', data)
}
</script>

<style>
@import './modal-styles.css';

</style>
