<template>
  <div class="file-list-container">
    <div class="file-card">
      <!-- 搜索框和批量操作栏 -->
      <div class="search-and-batch-actions">
        <div class="left-section">
          <h3>分享文件</h3>
          <div class="action-buttons">
            <button class="batch-btn" @click="handleBatchRemove" :disabled="!selectedFiles.length">
              <i class="fas fa-trash"></i> 移出分享
            </button>
            <button class="batch-btn" @click="handleRefresh">
              <i class="fas fa-sync-alt"></i> 刷新
            </button>
          </div>
        </div>
        <div class="right-section">
          <div class="search-controls">
            <input
              type="text"
              v-model="searchQuery"
              placeholder="搜索文件路径..."
              class="search-input"
              @keyup.enter="handleSearch"
            />
            <button class="batch-btn" @click="handleSearch">
              <i class="fas fa-search"></i> 搜索
            </button>
            <button v-if="searchQuery" class="batch-btn" @click="handleClearSearch">
              <i class="fas fa-times"></i> 清除搜索
            </button>
          </div>
        </div>
      </div>

      <!-- 加载状态 -->
      <div v-if="loading" class="empty-state">
        <div class="loading-spinner"></div>
        加载中...
      </div>

      <!-- 空状态 -->
      <div v-else-if="!shareFiles.length" class="empty-state">
        <div class="icon">
          <i class="fas fa-folder-open"></i>
        </div>
        <div class="title">暂无分享文件</div>
        <div class="description">
          请先在文件管理页面添加文件到分享列表
        </div>
      </div>

      <!-- 分享文件列表 -->
      <div v-else class="table-wrapper">
        <div class="table-min">
          <table class="file-table">
            <thead>
              <tr>
                <th><input type="checkbox" @change="toggleAll" :checked="isAllChecked" /></th>
                <th>文件名</th>
                <th>文件ID</th>
                <th>用户ID</th>
                <th>文件路径</th>
                <th>文件大小</th>
                <th>文件类型</th>
                <th>分享日期</th>
                <!-- <th>是否已分享</th> -->
                <th>操作</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="file in shareFiles" :key="file.id">
                <td><input type="checkbox" v-model="selectedFiles" :value="file.id" /></td>
                <td>
                  <i :class="getFileTypeIcon(file.file_type)" class="file-type-icon"></i>
                  {{ file.file_name }}
                </td>
                <td>
                  <span class="file-id">{{ file.id || '-' }}</span>
                </td>
                <td>
                  <span class="user-id">{{ file.user_id || '-' }}</span>
                </td>
                <td>{{ file.file_path }}</td>
                <td>{{ formatFileSize(file.file_size) }}</td>
                <td>{{ getFileTypeText(file.file_type) }}</td>
                <td>{{ formatDate(file.share_time) }}</td>
                <!-- <td>
                  <span v-if="file.has_share" class="share-status active">已分享</span>
                  <span v-else class="share-status">未分享</span>
                </td> -->
                <td>
                  <button class="action-btn" @click="handleCreateShare(file)">
                    创建分享
                  </button>
                  <button class="action-btn" @click="handleRemoveFile(file)" style="margin-left: 8px;">
                    移出列表
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <!-- 分页 -->
      <div class="pagination-container" v-if="pagination.total_pages > 1">
        <span class="page-info">
          第 {{ pagination.page }} 页，共 {{ pagination.total_pages }} 页 ({{ pagination.total }} 项)
        </span>
        
        <div class="pagination">
          <button :disabled="!pagination.has_prev" @click="changePage(pagination.page - 1)">
            <i class="fas fa-chevron-left"></i> 上一页
          </button>
          <button 
            v-for="page in getPageNumbers()" 
            :key="page" 
            :class="{ 'active': page === pagination.page }" 
            @click="changePage(page)"
            v-show="page !== '...'"
          >
            {{ page }}
          </button>
          <span v-if="getPageNumbers().includes('...')" class="page-ellipsis">...</span>
          <button :disabled="!pagination.has_next" @click="changePage(pagination.page + 1)">
            下一页 <i class="fas fa-chevron-right"></i>
          </button>
        </div>
      </div>
    </div>

    <!-- 创建分享弹窗 -->
    <ShareEditModal
      :visible="createShareModal.visible"
      mode="create"
      :selected-file="createShareModal.selectedFile"
      :loading="createShareModal.loading"
      @close="closeCreateShareModal"
      @confirm="handleCreateShareConfirm"
      @select-file="showFileSelector"
    />
  </div>
</template>

<script setup>
import { ref, computed, onMounted, inject } from 'vue'
import { ensureVisitJwt, getVisitJwt } from '@/utils/auth.js'
import ShareEditModal from './ShareEditModal.vue'
import '@/panel/child/file/file-list.css'
import '@/panel/child/share/share-styles.css'
import '@/panel/child/share/modal-styles.css'

// 注入全局组件
const notification = inject('notification')
const confirm = inject('confirm')

// 响应式数据
const shareFiles = ref([])
const selectedFiles = ref([])
const searchQuery = ref('')
const loading = ref(false)
const currentPage = ref(1)
const pagination = ref({
  page: 1,
  per_page: 20,
  total: 0,
  total_pages: 1,
  has_next: false,
  has_prev: false
})

// 创建分享弹窗状态
const createShareModal = ref({
  visible: false,
  selectedFile: null,
  loading: false
})

// Emits
// eslint-disable-next-line no-undef
const emit = defineEmits(['create-share'])

// API调用辅助函数
const apiCall = async (action, data = {}) => {
  try {
    // 确保有访问JWT
    const hasJwt = await ensureVisitJwt()
    if (!hasJwt) {
      notification.open({
        type: 'error',
        title: '认证失败',
        message: '请重新登录',
        onClose: () => window.location.reload()
      })
      return null
    }

    const formData = new FormData()
    formData.append('action', action)
    
    // 添加JWT访问令牌
    const visitJwt = getVisitJwt()
    if (visitJwt) {
      formData.append('jwt', visitJwt)
    }
    
    // 添加其他参数
    for (const [key, value] of Object.entries(data)) {
      if (value !== null && value !== undefined && value !== '') {
        formData.append(key, value)
      }
    }

    const response = await fetch('/api/share/share-api.php', {
      method: 'POST',
      body: formData,
      credentials: 'include'
    })

    const result = await response.json()
    
    if (!result.success) {
      throw new Error(result.message || '操作失败')
    }
    
    return result
  } catch (error) {
    console.error('API调用失败:', error)
    throw error
  }
}

// 计算属性
const isAllChecked = computed(() => {
  return shareFiles.value.length > 0 && selectedFiles.value.length === shareFiles.value.length
})

// 方法
const toggleAll = () => {
  if (isAllChecked.value) {
    selectedFiles.value = []
  } else {
    selectedFiles.value = shareFiles.value.map(file => file.id)
  }
}

const getFileTypeIcon = (type) => {
  const iconMap = {
    image: 'fas fa-image',
    video: 'fas fa-video',
    audio: 'fas fa-music',
    document: 'fas fa-file-alt',
    text: 'fas fa-file-text',
    archive: 'fas fa-file-archive',
    other: 'fas fa-file'
  }
  return iconMap[type] || iconMap.other
}

const getFileTypeText = (type) => {
  const typeMap = {
    image: '图片',
    video: '视频',
    audio: '音频',
    document: '文档',
    text: '文本',
    archive: '压缩包',
    other: '其他'
  }
  return typeMap[type] || '未知'
}

const formatFileSize = (bytes) => {
  if (bytes === 0) return '0 B'
  const k = 1024
  const sizes = ['B', 'KB', 'MB', 'GB']
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  return parseFloat((bytes / Math.pow(k, i)).toFixed(1)) + ' ' + sizes[i]
}

const formatDate = (dateStr) => {
  return new Date(dateStr).toLocaleString('zh-CN')
}

const getPageNumbers = () => {
  const total = pagination.value.total_pages
  const current = pagination.value.page
  const pages = []
  
  if (total <= 7) {
    for (let i = 1; i <= total; i++) {
      pages.push(i)
    }
  } else {
    pages.push(1)
    if (current > 4) pages.push('...')
    
    const start = Math.max(2, current - 1)
    const end = Math.min(total - 1, current + 1)
    
    for (let i = start; i <= end; i++) {
      pages.push(i)
    }
    
    if (current < total - 3) pages.push('...')
    pages.push(total)
  }
  
  return pages
}

const changePage = (page) => {
  if (page >= 1 && page <= pagination.value.total_pages) {
    currentPage.value = page
    loadShareFiles()
  }
}

const handleSearch = () => {
  currentPage.value = 1
  loadShareFiles()
}

const handleRefresh = () => {
  searchQuery.value = ''
  currentPage.value = 1
  loadShareFiles()
}

const handleClearSearch = () => {
  searchQuery.value = ''
  currentPage.value = 1
  loadShareFiles()
}

const handleCreateShare = (file) => {
  createShareModal.value = {
    visible: true,
    selectedFile: file,
    loading: false
  }
}

const closeCreateShareModal = () => {
  createShareModal.value.visible = false
}

const showFileSelector = () => {
  // 这里可以扩展为文件选择器，暂时使用通知提示
  notification.open({
    type: 'info',
    title: '提示',
    message: '请在列表中点击"创建分享"按钮选择文件',
    duration: 3000
  })
}

const handleCreateShareConfirm = async (formData) => {
  createShareModal.value.loading = true
  
  try {
    const result = await apiCall('create_share', {
      file_id: createShareModal.value.selectedFile.id,
      share_name: formData.shareName,
      access_password: formData.accessPassword,
      extension: formData.extension
    })
    
    if (result) {
      notification.open({
        type: 'success',
        title: '创建成功',
        message: `分享"${formData.shareName}"已创建`,
        duration: 3000
      })
      
      closeCreateShareModal()
      emit('create-share', result.data)
      await loadShareFiles()
    }
  } catch (error) {
    notification.open({
      type: 'error',
      title: '创建失败',
      message: error.message
    })
  } finally {
    createShareModal.value.loading = false
  }
}

const handleRemoveFile = async (file) => {
  confirm.open({
    title: '确认移除',
    message: `确定要将"${file.file_name}"从分享列表中移除吗？`,
    onConfirm: async () => {
      try {
        const result = await apiCall('remove_share_file', {
          file_id: file.id
        })
        
        if (result) {
          notification.open({
            type: 'success',
            title: '移除成功',
            message: '文件已从分享列表中移除',
            duration: 3000
          })
          await loadShareFiles()
        }
      } catch (error) {
        notification.open({
          type: 'error',
          title: '操作失败',
          message: error.message
        })
      }
    }
  })
}

const handleBatchRemove = async () => {
  if (!selectedFiles.value.length) {
    notification.open({
      type: 'warning',
      title: '提示',
      message: '请先选择要移除的文件',
      duration: 3000
    })
    return
  }
  
  confirm.open({
    title: '确认批量移除',
    message: `确定要将选中的 ${selectedFiles.value.length} 个文件从分享列表中移除吗？`,
    onConfirm: async () => {
      try {
        const promises = selectedFiles.value.map(fileId => {
          return apiCall('remove_share_file', {
            file_id: fileId
          })
        })
        
        await Promise.all(promises)
        selectedFiles.value = []
        
        notification.open({
          type: 'success',
          title: '批量移除成功',
          message: '已将选中文件从分享列表中移除',
          duration: 3000
        })
        
        await loadShareFiles()
      } catch (error) {
        notification.open({
          type: 'error',
          title: '批量移除失败',
          message: error.message
        })
      }
    }
  })
}

const loadShareFiles = async () => {
  try {
    loading.value = true
    
    // 确保有访问JWT
    const hasJwt = await ensureVisitJwt()
    if (!hasJwt) {
      notification.open({
        type: 'error',
        title: '认证失败',
        message: '请重新登录',
        onClose: () => window.location.reload()
      })
      return
    }
    
    // 构建查询参数
    const params = new URLSearchParams({
      page: currentPage.value,
      per_page: 20
    })
    
    if (searchQuery.value.trim()) {
      params.append('action', 'search_share_files')
      params.append('keyword', searchQuery.value.trim())
    } else {
      params.append('action', 'list_share_files')
    }
    
    // 添加JWT参数
    const visitJwt = getVisitJwt()
    if (visitJwt) {
      params.append('jwt', visitJwt)
    }
    
    const response = await fetch(`/api/share/share-api.php?${params.toString()}`, {
      method: 'GET',
      credentials: 'include'
    })
    
    const result = await response.json()
    if (result.success) {
      shareFiles.value = result.data || []
      pagination.value = result.pagination || {}
    } else {
      throw new Error(result.message)
    }
  } catch (error) {
    console.error('加载分享文件失败:', error)
    notification.open({
      type: 'error',
      title: '加载失败',
      message: error.message
    })
  } finally {
    loading.value = false
  }
}

// 生命周期
onMounted(() => {
  loadShareFiles()
})

// 暴露方法给父组件
// eslint-disable-next-line no-undef
defineExpose({
  refresh: loadShareFiles
})
</script>

<style scoped>
.table-min {
  min-width: 1400px;
}

.file-id, .user-id {
  font-family: monospace;
  font-size: 13px;
  color: #b0b0b0;
  background: none;
  padding: 2px 6px;
  border-radius: 3px;
  border: none;
  font-weight: bold;
  letter-spacing: 0.5px;
}

.file-id.selected, .user-id.selected {
  color: #2196f3;
  font-weight: bold;
}
</style>
