<template>
  <div class="file-list-container">
    <div class="file-card">
      <!-- 页面标题和刷新按钮 -->
      <div class="search-and-batch-actions">
        <div class="left-section">
          <h3>文件管理</h3>
        </div>
        <div class="right-section">
          <button class="batch-btn" @click="handleRefresh">
            <i class="fas fa-sync-alt"></i> 刷新
          </button>
        </div>
      </div>

      <!-- 加载状态 -->
      <div v-if="loading" class="empty-state">
        <div class="loading-spinner"></div>
        加载中...
      </div>

      <!-- 空状态 -->
      <div v-else-if="!files.length" class="empty-state">
        <div class="icon">
          <i class="fas fa-file"></i>
        </div>
        <div class="title">暂无分享文件</div>
        <div class="description">
          系统中暂无用户创建的分享文件
        </div>
      </div>

      <!-- 分享文件列表 -->
      <div v-else class="table-wrapper">
        <div class="table-min">
          <table class="file-table">
            <thead>
              <tr>
                <th>文件名</th>
                <th>用户ID</th>
                <th>文件大小</th>
                <th>文件类型</th>
                <th>分享时间</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="file in files" :key="file.id">
                <td>
                  <i :class="getFileTypeIcon(file.file_type)" class="file-type-icon"></i>
                  {{ file.file_name }}
                </td>
                <td>
                  <span class="user-id">{{ file.user_id || '-' }}</span>
                </td>
                <td>{{ formatFileSize(file.file_size) }}</td>
                <td>{{ file.file_type || '-' }}</td>
                <td>{{ formatDate(file.share_time) }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <!-- 分页 -->
      <div class="pagination-container" v-if="pagination.total_pages > 1">
        <span class="page-info">
          第 {{ pagination.page }} 页，共 {{ pagination.total_pages }} 页 ({{ pagination.total }} 项)
        </span>
        
        <div class="pagination">
          <button :disabled="!pagination.has_prev" @click="changePage(pagination.page - 1)">
            <i class="fas fa-chevron-left"></i> 上一页
          </button>
          <button 
            v-for="page in getPageNumbers()" 
            :key="page" 
            :class="{ 'active': page === pagination.page }" 
            @click="changePage(page)"
            v-show="page !== '...'"
          >
            {{ page }}
          </button>
          <span v-if="getPageNumbers().includes('...')" class="page-ellipsis">...</span>
          <button :disabled="!pagination.has_next" @click="changePage(pagination.page + 1)">
            下一页 <i class="fas fa-chevron-right"></i>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, inject } from 'vue'
import { request } from '@/utils/auth.js'
import '@/panel/child/file/file-list.css'
import '@/panel/child/share/share-styles.css'

// 注入全局组件
const notification = inject('notification')

// 响应式数据
const files = ref([])
const loading = ref(false)
const currentPage = ref(1)
const pagination = ref({
  page: 1,
  per_page: 20,
  total: 0,
  total_pages: 1,
  has_next: false,
  has_prev: false
})

// 获取所有分享文件列表
const getAllShareFiles = async (page = 1) => {
  loading.value = true
  try {
    const params = {
      action: 'get_all_share_files',
      page: page,
      per_page: pagination.value.per_page
    }
    
    const result = await request('/api/share/admin-api.php', {
      method: 'POST',
      body: JSON.stringify(params)
    })
    
    // 修改这里：将 result.status 改为 result.success
    if (result.success) {
      files.value = result.data || []
      pagination.value = result.pagination || {
        page: 1,
        per_page: 20,
        total: 0,
        total_pages: 1,
        has_next: false,
        has_prev: false
      }
      currentPage.value = page
    } else {
      throw new Error(result.message || '获取分享文件列表失败')
    }
  } catch (error) {
    console.error('获取分享文件列表失败:', error)
    notification.open({
      type: 'error',
      title: '获取失败',
      message: error.message || '获取分享文件列表失败'
    })
    files.value = []
  } finally {
    loading.value = false
  }
}

// 获取文件类型图标
const getFileTypeIcon = (type) => {
  const iconMap = {
    'image': 'fas fa-image file-type-icon image',
    'video': 'fas fa-video file-type-icon video',
    'audio': 'fas fa-music file-type-icon audio',
    'document': 'fas fa-file-alt file-type-icon document',
    'archive': 'fas fa-file-archive file-type-icon archive',
    'pdf': 'fas fa-file-pdf file-type-icon document',
    'text': 'fas fa-file-alt file-type-icon document',
    'code': 'fas fa-code file-type-icon document',
    'unknown': 'fas fa-file file-type-icon'
  }
  return iconMap[type] || iconMap['unknown']
}

// 格式化文件大小
const formatFileSize = (size) => {
  if (size === null || size === undefined) return '-'
  
  const units = ['B', 'KB', 'MB', 'GB', 'TB']
  let unitIndex = 0
  let fileSize = size
  
  while (fileSize >= 1024 && unitIndex < units.length - 1) {
    fileSize /= 1024
    unitIndex++
  }
  
  return unitIndex === 0 
    ? fileSize + ' ' + units[unitIndex]
    : fileSize.toFixed(2) + ' ' + units[unitIndex]
}

// 格式化日期
const formatDate = (dateString) => {
  if (!dateString) return '-'
  
  const date = new Date(dateString)
  const now = new Date()
  const diffTime = Math.abs(now - date)
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
  
  // 如果是今天，显示时间
  if (diffDays <= 1) {
    return date.toLocaleTimeString('zh-CN', {
      hour: '2-digit',
      minute: '2-digit'
    })
  }
  
  // 如果是今年，显示月日
  if (date.getFullYear() === now.getFullYear()) {
    return date.toLocaleDateString('zh-CN', {
      month: '2-digit',
      day: '2-digit'
    })
  }
  
  // 否则显示完整日期
  return date.toLocaleDateString('zh-CN')
}

// 获取分页数字
const getPageNumbers = () => {
  const pages = []
  const totalPages = pagination.value.total_pages
  const currentPage = pagination.value.page
  
  if (totalPages <= 7) {
    // 如果总页数小于等于7，显示所有页码
    for (let i = 1; i <= totalPages; i++) {
      pages.push(i)
    }
  } else {
    // 如果总页数大于7，显示省略模式
    if (currentPage <= 4) {
      // 当前页在前4页
      for (let i = 1; i <= 5; i++) {
        pages.push(i)
      }
      pages.push('...')
      pages.push(totalPages)
    } else if (currentPage >= totalPages - 3) {
      // 当前页在后4页
      pages.push(1)
      pages.push('...')
      for (let i = totalPages - 4; i <= totalPages; i++) {
        pages.push(i)
      }
    } else {
      // 当前页在中间
      pages.push(1)
      pages.push('...')
      for (let i = currentPage - 1; i <= currentPage + 1; i++) {
        pages.push(i)
      }
      pages.push('...')
      pages.push(totalPages)
    }
  }
  
  return pages
}

// 切换页面
const changePage = (page) => {
  if (page >= 1 && page <= pagination.value.total_pages) {
    getAllShareFiles(page)
  }
}

// 处理刷新
const handleRefresh = () => {
  getAllShareFiles(1)
}

// 组件挂载时获取数据
onMounted(() => {
  getAllShareFiles(1)
})
</script>

<style scoped>
/* 可以在这里添加特定的样式 */
.user-id {
  color: #6c757d;
  font-family: 'Courier New', monospace;
  font-size: 13px;
}

.status-active {
  color: #28a745;
  font-weight: bold;
}

.status-inactive {
  color: #6c757d;
}
</style>